<template>
    <div class="app-table-scroll-div">
        <div>
            <el-row>
                <el-col>
                    <el-breadcrumb separator="/">
                        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
                        <el-breadcrumb-item></el-breadcrumb-item>
                    </el-breadcrumb>
                </el-col>
            </el-row>
            <el-row class="query-form">

                <el-col :span="4">
                    <!--<el-input v-model="queryItem.appName" placeholder=""></el-input>-->

                    <el-select v-model="queryItem.appName"
                               placeholder=""
                               filterable
                               remote
                               :remote-method="handleAppAutoPrompt" @change="onSearch">
                        <el-option v-for="item in appsByName"
                                   :key="item.id"
                                   :label="item.name"
                                   :value="item.name">
                        </el-option>

                    </el-select>

                </el-col>

                <el-col :span="4">
                    <el-input v-model="queryItem.appId" placeholder="AppId"></el-input>
                </el-col>

                <el-col :span="4">
                    <el-select v-model="queryItem.orgId" placeholder="" @change="onSearch">
                        <el-option v-for="item in orgs"
                                   :key="item.id"
                                   :label="item.name"
                                   :value="item.id"></el-option>
                    </el-select>
                </el-col>

                <el-col :span="4">
                    <el-select v-model="queryItem.developers"
                               placeholder=""
                               filterable
                               remote
                               :remote-method="handleUserAutoPrompt" @change="onSearch">
                        <el-option v-for="item in usersByName"
                                   :key="item.workNumber"
                                   :label="item.userName"
                                   :value="item.workNumber">
                        </el-option>
                    </el-select>
                </el-col>

                <el-col :span="2" :offset="1">
                    <el-button @click="resetQueryForm"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="onSearch"></el-button>
                </el-col>
                <el-button type="primary" fixed="right" @click="addApp" class="add_button" v-if="isAdmin"></el-button>
            </el-row>
        </div>
        <br/>

        <div>
            <el-table :data="apps" style="width: 100%" empty-text="" border fit>
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-form label-position="left" inline class="demo-table-expand">
                            <h5 style="margin-bottom: 10px">URL</h5>
                            <div v-for="envUrl in props.row.envUrls" style="height: 20px;">
                                <label style="font-weight: bold">{{ envUrl.envName + ":"}}</label>
                                <span style="font:italic bold 12px/30px arial,sans-serif;">{{ envUrl.url }} </span>
                            </div>
                        </el-form>
                    </template>
                </el-table-column>

                <el-table-column label="ID" prop="id" align="center" sortable></el-table-column>
                <el-table-column label="" prop="name" align="center"></el-table-column>
                <el-table-column label="ID" prop="appId" align="center"></el-table-column>
                <el-table-column label="" prop="orgDto.name" align="center"></el-table-column>

                <el-table-column label="HA" align="center">
                    <template slot-scope="props">
                        <el-tag type="success" v-if="props.row.enableHa"></el-tag>
                        <el-tag type="danger" v-else></el-tag>
                    </template>
                </el-table-column>

                <el-table-column label="" align="center">
                    <template slot-scope="props">
                        <label v-for="item in props.row.userDtos">{{item.realName + " "}}</label>
                    </template>
                </el-table-column>

                <el-table-column label="" align="center">
                    <template slot-scope="props">
                        <label v-for="item in props.row.testUserDtos">{{item.realName + " "}}</label>
                    </template>
                </el-table-column>

                <el-table-column label="" align="center" width="200px" v-if="isAdmin">
                    <template slot-scope="props">
                        <el-button @click="handleEdit(props.row)" size="small"></el-button>
                        <el-button @click="handleDelete(props.row.id)" type="danger" size="small"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>

        <div align='center' style="margin-top: 10px">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size.sync="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </div>

        <el-dialog title="" :visible.sync="dialogVisible" top="50px">
            <el-form label-width="80px" label-position="center" :model="inEditApp" ref="inEditAppForm" :rules="rules">
                <el-form-item label="" prop="name">
                    <el-input v-model="inEditApp.name" @change="changeAppName"></el-input>
                </el-form-item>

                <el-form-item label="ID" prop="appId">
                    <el-input v-model="inEditApp.appId"></el-input>
                </el-form-item>

                <el-form-item label="" prop="orgId" class="app-drop-down">
                    <el-select v-model="inEditApp.orgId" placeholder="">
                        <el-option v-for="item in orgs" :key="item.id" :label="item.name"
                                   :value="item.id"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="Url" prop="envUrls" class="app-drop-down">

                    <el-select v-model="inEditAddEnvName" placeholder="" @change="addEnvUrl(inEditAddEnvName)"
                               class="selectEnv">
                        <el-option v-for="item in tempEnvs" :key="item.envName" :label="item.envName"
                                   :value="item.envName">
                        </el-option>
                    </el-select>

                    <div v-for="envUrl in inEditEnvUrls">
                        <el-form-item>
                            <el-row>
                                <el-col :span="4">
                                    <!--<span>{{envUrl.envName}}</span>-->
                                    <el-input v-model="envUrl.envName" :readonly="true"></el-input>
                                </el-col>
                                <el-col :span="16">
                                    <el-input v-model="envUrl.url" placeholder="url"></el-input>
                                </el-col>
                                <el-col :span="4">
                                    <el-button @click="handleDeleteEnvUrl(envUrl.envName)" type="danger" size="small"
                                               class="deleteEnv">
                                    </el-button>
                                </el-col>
                            </el-row>
                        </el-form-item>
                    </div>
                </el-form-item>

                <el-form-item label="HA" prop="enableHa">
                    <el-switch
                            v-model="inEditApp.enableHa"
                            active-color="#13ce66"
                            inactive-color="#ff4949">
                    </el-switch>
                </el-form-item>

                <el-form-item label="" prop="tests">
                    <el-select v-model="inEditApp.tests"
                               placeholder=""
                               multiple
                               filterable
                               remote
                               :remote-method="handleTestUserAutoPrompt">
                        <el-option v-for="item in testUsersByName"
                                   :key="item.workNumber"
                                   :label="item.userName"
                                   :value="item.workNumber">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="" prop="developers">
                    <el-select v-model="inEditApp.developers"
                               placeholder=""
                               multiple
                               filterable
                               remote
                               :remote-method="handleUserAutoPrompt">
                        <el-option v-for="item in usersByName"
                                   :key="item.workNumber"
                                   :label="item.userName"
                                   :value="item.workNumber">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="" prop="description">
                    <el-input v-model="inEditApp.description" type="textarea"></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="onSubmit('inEditAppForm')"></el-button>
                    <el-button @click="resetForm" v-if="!inEditApp.id"></el-button>
                    <el-button @click="onClose" class="close-button"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';

    export default {

        methods: {
            zoneTypeFormat:function(row, column) {
                var type = row[column.property];
                if (type == 'capital') {
                    return '';
                }

                return '';
            },
            handleEdit(appDto) {
                this.inEditApp = {
                    id: appDto.id,
                    name: appDto.name,
                    appId: appDto.appId,
                    appType: appDto.appType,
                    orgId: appDto.orgId,
                    appLevel: appDto.appLevel,
                    influenceScope: appDto.influenceScope,
                    serviceType: appDto.serviceType,
                    zoneType: appDto.zoneType,
                    enableHa: appDto.enableHa,
                    // ,
                    developers: appDto.developers.split(","),
                    tests: appDto.tests != "" ? appDto.tests.split(",") : [],
                    envUrls: appDto.envUrls,
                    description: appDto.description,
                };

                // inEditEnvUrls, 
                //, append
                this.inEditEnvUrls = [];

                let data = {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                };

                this.tempEnvs = [];
                //env name 
                for (var item of this.envs) {
                    let envItem = {
                        envName: item.name,
                    };
                    this.tempEnvs.push(envItem);
                }

                if (appDto.envUrls != null) {
                    for (var value of appDto.envUrls) {

                        //env ,
                        this.tempEnvs.splice(this.tempEnvs.findIndex(item => item.envName === value.envName), 1);

                        //Item, 
                        let envItem = {
                            envName: value.envName,
                            url: value.url
                        };
                        //edit  env-url
                        this.inEditEnvUrls.push(envItem);
                    }
                }

                // app  usersByName
                this.$store.dispatch('initUsersByName', appDto.userDtos);

                this.$store.dispatch('initTestUsersByName', appDto.testUserDtos);

                //
                this.inEditAddEnvName = null;
                this.dialogVisible = true;
            },

            addEnvUrl(envName) {
                // envName,  env-url item
                let newEnvItem = {
                    envName: envName,
                    url: ""
                };
                //env-url,  inEditEnvUrls
                this.inEditEnvUrls.push(newEnvItem);

            },

            handleDeleteEnvUrl(envName) {
                //env-url,  inEditEnvUrls  env-url
                this.inEditEnvUrls.splice(this.inEditEnvUrls.findIndex(item => item.envName === envName), 1);
            },

            handleDelete(appId) {
                this.$confirm('')
                    .then(() => {
                        let data = {
                            appId: appId,
                            pageSize: this.pageSize,
                            page: this.currentPage,
                        };
                        this.$store.dispatch('deleteAppById', data);
                    })
                    .catch(() => {
                    });
            },

            handleSizeChange(data) {
                this.pageSize = data;

                this.$store.dispatch('queryApps', {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                    queryApp: this.queryItem
                });

            },

            handleCurrentChange(data) {
                this.currentPage = data;

                this.$store.dispatch('queryApps', {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                    queryApp: this.queryItem
                });

            },

            //appNameappName
            handleAppAutoPrompt(data) {
                if (data !== '') {
                    this.$store.dispatch('fuzzyQueryAppsByAppName', {appName: data});
                }
            },

            //appName
            handleAppAutoPromptAppId(data) {
                if (data !== '') {
                    this.$store.dispatch('queryAppsByAppId', {appId: data});
                }
            },

            //userName
            handleUserAutoPrompt(data) {
                if (data !== '') {
                    this.$store.dispatch('queryUsersByUserName', {userName: data});
                }
            },

            //userName
            handleTestUserAutoPrompt(data) {
                if (data !== '') {
                    this.$store.dispatch('queryUsersByUserName', {userName: data, type: "test"});
                }
            },

            addApp() {
                this.inEditApp = {
                    id: null,
                    name: '',
                    appId: '',
                    appType: 'SpringBoot',
                    orgId: null,
                    appLevel: '',
                    influenceScope: '',
                    serviceType: '',
                    zoneType: 'default',
                    enableHa: true,
                    developers: [],
                    tests: [],
                    envUrls: [],
                    description: ''
                };

                //
                this.inEditAddEnvName = null;

                // app ,  tempEnvs
                this.tempEnvs = [];
                //envs  env name,  env
                for (var item of this.envs) {
                    let envItem = {
                        envName: item.name,
                    };
                    this.tempEnvs.push(envItem);
                }

                //appenv-url
                this.inEditEnvUrls = [];

                for (let env of this.envs) {
                    let envItem = {
                        envName: env.name,
                        url: ''
                    };
                    this.inEditEnvUrls.push(envItem);
                }

                this.dialogVisible = true;
            },

            changeAppName(value) {
                for (let inEditEnvUrl of this.inEditEnvUrls) {
                    inEditEnvUrl.url = value;
                }
            },

            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let self = this;
                        for (var item of this.inEditEnvUrls) {
                            if (item.url == null || item.url === '') {
                                //alert("url can not be empty");
                                this.$message.error(
                                    'url can not be empty',
                                );
                                return;
                            }
                        }
                        this.dialogVisible = false;
                        // env url
                        self.inEditApp.envUrls = this.inEditEnvUrls;
                        //  , add/update
                        //**,**,**
                        let workNumbers = self.inEditApp.developers.join();
                        self.inEditApp.developers = workNumbers;

                        let testUserNumbers = self.inEditApp.tests.join();
                        self.inEditApp.tests = testUserNumbers;

                        let data = {
                            //
                            queryApp: this.queryItem,
                            newApp: self.inEditApp,
                            pageSize: this.pageSize,
                            page: this.currentPage,
                        };
                        if (data.newApp.id == null) {
                            this.$store.dispatch('createNewApp', data);
                        } else {
                            this.$store.dispatch('updateApp', data);
                        }
                    } else {
                        return false;
                    }
                });
            },

            appNameCheck() {
                this.$store.dispatch('queryAppsByAppName', data);
                //apps 
            },

            onClose() {
                this.dialogVisible = false;
            },

            onSearch() {
                this.currentPage = 1;
                this.$store.dispatch('queryApps', {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                    queryApp: this.queryItem
                });
            },
            resetForm() {
                this.inEditApp = {
                    id: null,
                    name: '',
                    appId: '',
                    appType: '',
                    orgId: null,
                    appLevel: '',
                    influenceScope: '',
                    serviceType: '',
                    zoneType: null,
                    enableHa: false,
                    developers: [],
                    envUrls: [],
                    tests: [],
                    description: ''
                }
            },
            resetQueryForm() {
                this.queryItem = {
                    appId: null,
                    appName: '',
                    orgId: null,
                    developers: ''
                };
                this.onSearch();
            }
        },

        data: function () {
            return {
                dialogVisible: false,
                currentField: 'appName',
                currentValue: '',

                queryItem: {
                    appId: null,
                    appName: '',
                    orgId: null,
                    developers: ''
                },

                currentPage: 1,
                pageSize: 10,

                inEditEnvUrls: [],
                //envUrl temp 
                inEditAddEnvName: null,

                tempEnvs: [],

                inEditApp: {
                    id: null,
                    name: '',
                    appId: '',
                    appType: '',
                    orgId: null,
                    appLevel: '',
                    influenceScope: '',
                    serviceType: '',
                    zoneType: null,
                    enableHa: false,
                    //,
                    developers: [],
                    tests: [],
                    envUrls: [],
                    description: ''
                },
                rules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    appId: [
                        {required: true, message: 'appId', trigger: 'blur'}
                    ],
                    appType: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    orgId: [
                        {type: 'number', required: true, message: 'orgId', trigger: 'blur'}
                    ],
                    developers: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                }
            }
        },

        computed: {
            ...mapGetters({
                apps: 'getAllApps',
                appsFuzzyByName: 'getFuzzyAppsByName',
                appsByName: 'getAppsByName',
                usersByName: 'getUsersByName',
                //test
                testUsersByName: 'getTestUsersByName',
                total: 'getTotalApps',
                orgs: 'getAllOrgs',
                envs: 'getAllEnvs',
                isAdmin: 'getAdminResult'
            })
        },
        created() {
            this.$store.dispatch('fetchAllApps', {
                pageSize: this.pageSize,
                page: this.currentPage,
            });
            this.$store.dispatch('fetchAllOrgs', {
                pageSize: 9999,
                page: 1,
            });
            this.$store.dispatch('fetchAllEnvs', {
                pageSize: 9999,
                page: 1,
            });
        }
    };

</script>

<style>
    .query-form {
        margin-top: 20px;
        padding: 25px;
        background-color: #eef1f6;
    }

    .close-button {
        float: right;
    }

    .add_button {
        float: right;
    }

    div.el-form-item:last-child {
        margin-bottom: 0;
    }

    /*    .el-dialog__body {
            padding: 30px 200px;
        }*/

    .app-drop-down .el-select {
        width: 100%;
    }

    .selectEnv {
        margin-bottom: 5px;
    }

    .deleteEnv {
        margin-left: 2px;
        height: 38px;
        width: 76px;
    }

    .app-table-scroll-div {
        max-width: 1600px;
        min-width: 1200px;
    }

</style>




