<template>
    <div class="table-scroll-div">
        <div>
            <el-row>
                <el-col>
                    <el-breadcrumb separator="/">
                        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
                        <el-breadcrumb-item></el-breadcrumb-item>
                    </el-breadcrumb>
                </el-col>
            </el-row>
            <el-row class="query-form">
                <!--
                <el-col :span="2" :offset="1">
                    <el-button @click="resetQueryForm"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="onSearch"></el-button>
                </el-col>
                -->
                <el-button type="primary" fixed="right" @click="addEnv" class="add_button"  v-if="isAdmin"></el-button>
            </el-row>
        </div>
        <br/>

        <div>
            <el-table :data="envs" style="width: 100%" empty-text="" border fit>
                <el-table-column label="ID" prop="id" align="center" sortable width="80px"></el-table-column>
                <el-table-column label="" prop="name" align="center"></el-table-column>
                <el-table-column label="consul" prop="consul" align="center"></el-table-column>
                <el-table-column label="nginx" prop="nginx" align="center"></el-table-column>
                <el-table-column label="dns" prop="dns" align="center"></el-table-column>
                <el-table-column label="" prop="dockeryard" align="center"></el-table-column>
                <el-table-column label="" prop="description" align="center"></el-table-column>
                <el-table-column label="" align="center" width="200px" v-if="isAdmin">
                    <template slot-scope="props">
                        <el-button @click="handleEdit(props.row)" size="small"></el-button>
                        <el-button @click="handleDelete(props.row.id)" type="danger" size="small"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>

        <div align='center' style="margin-top: 10px">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size.sync="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </div>

        <el-dialog title="" :visible.sync="dialogVisible">
            <el-form label-width="100px" label-position="right" :model="inEditEnv" ref="inEditEnvForm" :rules="rules">
                <el-form-item label="" prop="name">
                    <el-input v-model="inEditEnv.name"></el-input>
                </el-form-item>
                <el-form-item label="consul" prop="consul">
                    <el-input v-model="inEditEnv.consul"></el-input>
                </el-form-item>
                <el-form-item label="nginx" prop="nginx">
                    <el-input v-model="inEditEnv.nginx"></el-input>
                </el-form-item>
                <el-form-item label="dns" prop="dns">
                    <el-input v-model="inEditEnv.dns"></el-input>
                </el-form-item>
                <el-form-item label="" prop="dockeryard">
                    <el-input v-model="inEditEnv.dockeryard"></el-input>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input v-model="inEditEnv.description" type="textarea"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit('inEditEnvForm')"></el-button>
                    <el-button @click="resetForm" v-if="!inEditEnv.id"></el-button>
                    <el-button @click="onClose" class="close-button"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';

    export default {

        methods: {
            handleEdit(envDto) {
                this.inEditEnv = {
                    id: envDto.id,
                    name: envDto.name,
                    consul: envDto.consul,
                    nginx: envDto.nginx,
                    dns: envDto.dns,
                    dockeryard: envDto.dockeryard,
                    extensions: {
                    },
                    description: envDto.description
                };
                this.dialogVisible = true;
            },
            handleDelete(envId) {
                this.$confirm('')
                    .then(() => {
                        let data = {
                            envId: envId,
                            pageSize: this.pageSize,
                            page: this.currentPage,
                        };
                        this.$store.dispatch('deleteEnvById', data);
                    })
                    .catch(() => {
                    });
            },
            handleSizeChange(data) {
                this.pageSize = data;
                this.$store.dispatch('queryEnvs', {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                    queryEnv: this.queryItem
                });
            },
            handleCurrentChange(data) {
                this.currentPage = data;
                this.$store.dispatch('queryEnvs', {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                    queryEnv: this.queryItem
                });
            },
            addEnv() {
                this.inEditEnv = {
                    id: null,
                    name: '',
                    consul: '',
                    dns: '',
                    nginx: '',
                    dockeryard: '',
                    extensions: {
                    },
                    description: ''
                };
                this.dialogVisible = true;
            },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let self = this;
                        this.dialogVisible = false;
                        let data = {
                            queryEnv: this.queryItem,
                            newEnv: self.inEditEnv,
                            pageSize: this.pageSize,
                            page: this.currentPage,
                        };
                        if (data.newEnv.id == null) {
                            this.$store.dispatch('createNewEnv', data);
                        } else {
                            this.$store.dispatch('updateEnv', data);
                        }
                    } else {
                        return false;
                    }
                });
            },
            onClose() {
                this.dialogVisible = false;
            },
            onSearch() {
                this.currentPage = 1;
                this.$store.dispatch('queryEnvs', {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                    queryEnv: this.queryItem
                });
            },
            resetForm() {
                this.inEditEnv = {
                    id: null,
                    name: '',
                    consul: '',
                    dns: '',
                    nginx: '',
                    dockeryard: '',
                    extensions: {
                    },
                    description: ''
                }
            },

            resetQueryForm() {
                this.queryItem= {
                }
            },

        },

        data: function () {
            return {
                dialogVisible: false,
                //currentField: 'id',

                //currentValue: '',
                fieldList: [{
                    value: 'id',
                    label: 'ID'
                }, {
                    value: 'name',
                    label: ''
                }],
                currentPage: 1,
                pageSize: 10,
                inEditEnv: {
                    id: null,
                    name: '',
                    consul: '',
                    dns: '',
                    nginx: '',
                    dockeryard: '',
                    extensions: {
                    },
                    description: ''
                },
                queryItem:{
                },
                rules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                }
            }
        },

        computed: {
            ...mapGetters({
                envs: 'getAllEnvs',
                clouds: 'getAllClouds',
                total: 'getTotalEnvs',
                isAdmin: 'getAdminResult'
            })
        },
        created() {
            this.$store.dispatch('fetchAllEnvs', {
                pageSize: this.pageSize,
                page: this.currentPage,
            });
        }

    }

</script>

<style>
    .query-form {
        margin-top: 20px;
        padding: 25px;
        background-color: #eef1f6;
    }

    .close-button {
        float: right;
    }

    .add_button {
        float: right;
    }
</style>




