<template>
    <div class="table-scroll-div">
        <div>
            <el-row>
                <el-col>
                    <el-breadcrumb separator="/">
                        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
                        <el-breadcrumb-item></el-breadcrumb-item>
                    </el-breadcrumb>
                </el-col>
            </el-row>
            <el-row class="query-form">

                <el-col :span="3">
                    <el-input v-model="queryItem.name" placeholder=""></el-input>
                </el-col>
                <el-col :span="3">
                    <el-input v-model="queryItem.cpu" placeholder="cpu"></el-input>
                </el-col>
                <el-col :span="3">
                    <el-input v-model="queryItem.memory" placeholder=""></el-input>
                </el-col>
                <el-col :span="3">
                    <el-input v-model="queryItem.disk" placeholder=""></el-input>
                </el-col>
                <el-col :span="2" :offset="1">
                    <el-button @click="resetQueryForm"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="onSearch"></el-button>
                </el-col>
                <el-button type="primary" fixed="right" @click="addSpecType" class="add_button" v-if="isAdmin"></el-button>
            </el-row>
        </div>
        <br/>

        <div>
            <el-table :data="specTypes" style="width: 100%" empty-text="" border fit>
                <el-table-column label="ID" prop="id" align="center" sortable></el-table-column>
                <el-table-column label="" prop="name" align="center"></el-table-column>
                <el-table-column label="CPU()" prop="cpu" align="center"></el-table-column>
                <el-table-column label="(MB)" prop="memory" align="center"></el-table-column>
                <el-table-column label="(MB)" prop="disk" align="center"></el-table-column>
                <el-table-column label="" prop="description" align="center"></el-table-column>

                <el-table-column label="" align="center" width="200px" v-if="isAdmin">
                    <template slot-scope="props">
                        <el-button @click="handleEdit(props.row)" size="small"></el-button>
                        <el-button @click="handleDelete(props.row.id)" type="danger" size="small"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>

        <div align='center' style="margin-top: 10px">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size.sync="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </div>

        <el-dialog title="" :visible.sync="dialogVisible">
            <el-form label-width="80px" label-position="right" :model="inEditSpecType" ref="inEditSpecTypeForm"
                     :rules="rules">
                <el-form-item label="" prop="name">
                    <el-input v-model="inEditSpecType.name"></el-input>
                </el-form-item>
                <el-form-item label="cpu()" prop="cpu">
                    <el-input v-model="inEditSpecType.cpu"></el-input>
                </el-form-item>
                <el-form-item label="(MB)" prop="memory">
                    <el-input v-model="inEditSpecType.memory"></el-input>
                </el-form-item>
                <el-form-item label="(MB)" prop="disk">
                    <el-input v-model="inEditSpecType.disk"></el-input>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input type="textarea" v-model="inEditSpecType.description"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit('inEditSpecTypeForm')"></el-button>
                    <el-button @click="resetForm" v-if="!inEditSpecType.id"></el-button>
                    <el-button @click="onClose" class="close-button"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';

    export default {

        methods: {
            handleEdit(specTypeDto) {
                this.dialogVisible = true;
                this.inEditSpecType = {
                    id: specTypeDto.id,
                    name: specTypeDto.name,
                    cpu: specTypeDto.cpu.toString(),
                    memory: specTypeDto.memory.toString(),
                    disk: specTypeDto.disk.toString(),
                    description: specTypeDto.description
                };
            },
            handleDelete(specTypeId) {
                this.$confirm('')
                    .then(() => {
                        let data = {
                            specTypeId: specTypeId,
                            pageSize: this.pageSize,
                            page: this.currentPage,
                        };
                        this.$store.dispatch('deleteSpecTypeById', data);
                    })
                    .catch(() => {
                    });
            },
            handleSizeChange(data) {
                this.pageSize = data;
                this.$store.dispatch('querySpecTypes', {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                    specTypeQuery: this.queryItem
                });
            },
            handleCurrentChange(data) {
                this.currentPage = data;
                this.$store.dispatch('querySpecTypes', {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                    specTypeQuery: this.queryItem
                });
            },
            addSpecType() {
                this.inEditSpecType = {
                    id: null,
                    name: '',
                    cpu: null,
                    memory: null,
                    disk: '',
                    description: ''
                };
                this.dialogVisible = true;
            },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let self = this;
                        this.dialogVisible = false;
                        let data = {
                            newSpecType: self.inEditSpecType,
                            pageSize: this.pageSize,
                            page: this.currentPage,
                        };
                        if (data.newSpecType.id == null) {
                            this.$store.dispatch('createNewSpecType', data);
                        } else {
                            this.$store.dispatch('updateSpecType', data);
                        }
                    } else {
                        return false;
                    }
                });
            },
            onClose() {
                this.dialogVisible = false;
            },
            onSearch() {
                this.currentPage = 1;
                this.$store.dispatch('querySpecTypes', {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                    specTypeQuery: this.queryItem
                });
            },
            resetForm() {
                this.inEditSpecType = {
                    id: null,
                    name: '',
                    cpu: null,
                    memory: null,
                    disk: null,
                    description: ''
                }
            },
            resetQueryForm() {
                this.queryItem = {
                    name: '',
                    cpu: null,
                    memory: null,
                    disk: null
                }
            }
        },

        data: function () {
            return {
                dialogVisible: false,
                currentField: 'id',
                currentValue: '',
                queryItem: {
                    name: '',
                    cpu: null,
                    memory: null,
                    disk: null
                },
                currentPage: 1,
                pageSize: 10,
                inEditSpecType: {
                    id: null,
                    name: '',
                    cpu: null,
                    memory: null,
                    disk: null,
                    description: ''
                },
                rules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    cpu: [
                        {required: true, message: 'cpu', trigger: 'blur'}
                    ],
                    memory: [
                        {required: true, message: 'memory', trigger: 'blur'}
                    ],
                    disk: [
                        {required: true, message: 'disk', trigger: 'blur'}
                    ],
                }
            }
        },

        computed: {
            ...mapGetters({
                specTypes: 'getAllSpecTypes',
                total: 'getTotalSpecTypes',
                isAdmin: 'getAdminResult'
            })
        },
        created() {
            this.$store.dispatch('fetchAllSpecTypes', {
                pageSize: this.pageSize,
                page: this.currentPage,
            });
        }

    }

</script>

<style>
    .query-form {
        margin-top: 20px;
        padding: 25px;
        background-color: #eef1f6;
    }

    .close-button {
        float: right;
    }

    .add_button {
        float: right;
    }
</style>




