<template>
    <div class="header">
        <div class="title" @click="returnHome"><i class="el-icon-star-off logo">&nbsp;Atlas</i></div>
        <div class="user-info">
            <el-dropdown @command="handleCommand">
                <span class="el-dropdown-link">
                    <img v-if="username != null" class="user-logo" src="../../assets/img/dog.jpg">
                    {{(username != null) ? username : ""}}
                </span>
                <el-dropdown-menu slot="dropdown" class="dropdown-menu">
                    <el-dropdown-item command="logout" v-if='isLogin'></el-dropdown-item>
                    <el-dropdown-item command="login" v-else></el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
            <div v-if="isPromptExpire" class="expire-prompt">
                <div></div>
                <div>
                    <el-button type="primary" @click="login"></el-button>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'

    export default {
        data: function () {
            return {
                isLogoutOnStartup: false
            }
        },
        computed: {
            ...mapGetters({
                isLogin: 'getLoginState',
                username: 'getUserName',
                isExpired: 'getExpireState',
            }),
            isPromptExpire: function () {
                return this.isExpired && this.isLogin;
            }
        },
        methods: {
            ...mapActions(['logout', 'checkExpired', 'refreshToken']),
            handleCommand(command) {
                if (command == 'logout') {
                    this.logout();
                } else if (command == 'login') {
                    this.login();
                }
            },
            returnHome() {
                this.$router.push('/');
            },
            onInterval: function () {
                if (!this.isLogin) {
                    // 
                    clearInterval(this.internalTimer);
                    clearInterval(this.refreshTokenTimer);

                    // 
                    if (!this.isLogoutOnStartup) {
                        this.$message.success("");
                    }
                } else {
                    // 
                    this.$store.dispatch('checkExpired');
                }
            },
            refreshToken: function () {
                this.$store.dispatch('refreshToken');
            },
            logout() {
                // token
                this.$store.dispatch('revokeToken');
            },
            login() {
                this.$store.dispatch('saveLastVisit', this.$route.fullPath);
                //
                this.$store.dispatch('login');
            }
        },
        created() {
            this.$store.dispatch('readLoginInfo');
            this.refreshToken();
        },
        beforeMount: function () {
            //
            if (!this.isLogin) {
                this.$message.warning("");
                this.isLogoutOnStartup = true;
            }
        },
        mounted: function () {
            // 500ms
            this.internalTimer = setInterval(this.onInterval.bind(this), 500);
            // 5access token
            this.refreshTokenTimer = setInterval(this.refreshToken.bind(this), 5 * 60 * 60 * 1000);
        },
        beforeDestroy: function () {
            //
            clearInterval(this.internalTimer);
            clearInterval(this.refreshTokenTimer);
        }
    };
</script>

<style scoped>

    .header {
        position: relative;
        box-sizing: border-box;
        width: 100%;
        height: 70px;
        font-size: 22px;
        line-height: 70px;
        color: #fff;
    }

    .header .title {
        display: table;
        cursor: pointer;
        margin-left: 25px;
        float: left;
    }

    .header .title:hover {
        color: #337ab7;
    }

    .header .logo {
        margin-right: 10px;
    }

    .user-info {
        float: right;
        padding-right: 50px;
        font-size: 16px;
        color: #fff;
    }

    .user-info .el-dropdown-link {
        position: relative;
        display: inline-block;
        padding-left: 50px;
        color: #fff;
        cursor: pointer;
        vertical-align: middle;
    }

    .user-info .user-logo {
        position: absolute;
        left: 0;
        top: 15px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }

    .el-dropdown-menu__item {
        text-align: center;
    }

    .expire-prompt {
        position: absolute;
        right: 0px;
        background: #fff;
        color: black;
        min-width: 150px;
        border-radius: 2px;
        border: 1px solid rgb(209, 219, 229);
        padding: 20px;
        z-index: 2000;
    }

    .expire-prompt div {
        text-align: center;
        margin: 0;
        line-height: 4em;
    }

</style>


