<template>
    <div class="table-scroll-div">
        <div>
            <el-row>
                <el-col>
                    <el-breadcrumb separator="/">
                        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
                        <el-breadcrumb-item>zone</el-breadcrumb-item>
                    </el-breadcrumb>
                </el-col>
            </el-row>
            <el-row class="query-form">
                <el-col :span="4">
                    <el-select v-model="currentEnv" placeholder="" @change="onSearch">
                        <el-option v-for="item in envs" :key="item.id" :label="item.name"
                                   :value="item.name"></el-option>
                    </el-select>
                </el-col>
                <el-col :span="4">
                    <el-input v-model="currentValue" placeholder="zone"></el-input>
                </el-col>
                <el-col :span="2" :offset="1">
                    <el-button @click="resetQueryForm"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="onSearch"></el-button>
                </el-col>
                <el-button type="primary" fixed="right" @click="addZone" class="add_button" v-if="isAdmin">zone
                </el-button>
            </el-row>
        </div>
        <br/>

        <div>
            <el-table :data="zones" style="width: 100%" empty-text="" border fit>
                <el-table-column label="ID" prop="id" align="center" sortable width="80px"></el-table-column>
                <el-table-column label="" prop="envName" align="center"></el-table-column>
                <el-table-column label="zone" prop="name" align="center"></el-table-column>
                <el-table-column label="k8shttp" prop="k8s" align="center"></el-table-column>
                <el-table-column label="k8srpc" prop="extensions.rpc" align="center"></el-table-column>
                <el-table-column label="" prop="extensions.cluster" align="center"></el-table-column>
                <el-table-column label="k8s" prop="k8sVersion" align="center"></el-table-column>
                <el-table-column label="dashboard" prop="extensions.dashboard" align="center"></el-table-column>
                <el-table-column label="" prop="description" align="center"></el-table-column>
                <el-table-column label="" align="center" width="200px" v-if="isAdmin">
                    <template slot-scope="props">
                        <el-button @click="handleEdit(props.row)" size="small"></el-button>
                        <el-button @click="handleDelete(props.row.id)" type="danger" size="small"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>

        <div align='center' style="margin-top: 10px">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size.sync="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </div>

        <el-dialog title="zone" :visible.sync="dialogVisible">
            <el-form label-width="100px" label-position="right" :model="inEditZone" ref="inEditZoneForm"
                     :rules="rules">
                <el-form-item label="" prop="env">
                    <el-select v-model="inEditZone.env" placeholder="" value-key="id">
                        <el-option v-for="item in envs" :key="item.id" :label="item.name"
                                   :value="item"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="inEditZone.name"></el-input>
                </el-form-item>
                <el-form-item label="k8shttp" prop="k8s">
                    <el-input v-model="inEditZone.k8s"></el-input>
                </el-form-item>
                <el-form-item label="k8srpc" prop="rpc">
                    <el-input v-model="inEditZone.extensions.rpc"></el-input>
                </el-form-item>
                <el-form-item label="" prop="cluster">
                    <el-input v-model="inEditZone.extensions.cluster"></el-input>
                </el-form-item>
                <el-form-item label="k8s" prop="cluster">
                    <el-input v-model="inEditZone.k8sVersion"></el-input>
                </el-form-item>
                <el-form-item label="dashboard" prop="dashboard">
                    <el-input v-model="inEditZone.extensions.dashboard"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="inEditZone.description" type="textarea"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit('inEditZoneForm')"></el-button>
                    <el-button @click="resetForm" v-if="!inEditZone.id"></el-button>
                    <el-button @click="onClose" class="close-button"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';

    export default {

        methods: {
            handleEdit(zoneDto) {
                this.dialogVisible = true;
                this.inEditZone = {
                    id: zoneDto.id,
                    name: zoneDto.name,
                    env: {
                        id: zoneDto.envId,
                        name: zoneDto.envName
                    },
                    envId: zoneDto.envId,
                    envName: zoneDto.envName,
                    k8s: zoneDto.k8s,
                    k8sVersion: zoneDto.k8sVersion,
                    extensions: {
                        dashboard: zoneDto.extensions && zoneDto.extensions.dashboard ? zoneDto.extensions.dashboard : '',
                        cluster: zoneDto.extensions && zoneDto.extensions.cluster ? zoneDto.extensions.cluster : '',
                        level: zoneDto.extensions && zoneDto.extensions.level != null ? zoneDto.extensions.level : 1,
                        rpc: zoneDto.extensions && zoneDto.extensions.rpc ? zoneDto.extensions.rpc : '',
                    },
                    description: zoneDto.description
                };
            },
            handleDelete(zoneId) {
                this.$confirm('')
                    .then(() => {
                        let data = {
                            zoneId: zoneId,
                            pageSize: this.pageSize,
                            page: this.currentPage,
                        };
                        this.$store.dispatch('deleteZoneById', data);
                    })
                    .catch(() => {
                    });
            },
            handleSizeChange(data) {
                this.pageSize = data;
                this.$store.dispatch('fetchAllZones', {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                });
            },
            handleCurrentChange(data) {
                this.currentPage = data;
                this.$store.dispatch('fetchAllZones', {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                });
            },
            addZone() {
                this.inEditZone = {
                    id: null,
                    name: '',
                    env: null,
                    k8s: '',
                    extensions: {
                        dashboard: '',
                        cluster: '',
                        level: 1,
                        rpc: ''
                    },
                    description: ''
                };
                this.dialogVisible = true;
            },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let self = this;

                        if (!self.inEditZone.extensions.cluster) {
                            this.$message.warning('k8s');
                        } else if (!self.inEditZone.extensions.dashboard) {
                            this.$message.warning('dashboard');
                        } else if (!Number.isInteger(self.inEditZone.extensions.level) || self.inEditZone.extensions.level < 0) {
                            this.$message.warning('0');
                        } else {
                            let data = {
                                envName: this.currentEnv,
                                zoneName: this.currentValue,
                                newZone: self.inEditZone,
                                pageSize: this.pageSize,
                                page: this.currentPage,
                            };

                            data.newZone.envId = self.inEditZone.env.id;
                            data.newZone.envName = self.inEditZone.env.name;
                            delete data.newZone.env;

                            if (data.newZone.id == null) {
                                this.$store.dispatch('createNewZone', data);
                            } else {
                                this.$store.dispatch('updateZone', data);
                            }

                            this.dialogVisible = false;
                        }
                    } else {
                        return false;
                    }
                });
            },
            onClose() {
                this.dialogVisible = false;
            },
            onSearch() {
                this.currentPage = 1;
                this.$store.dispatch('queryZones', {
                    pageSize: this.pageSize,
                    page: this.currentPage,
                    envName: this.currentEnv,
                    zoneName: this.currentValue
                });
            },
            resetForm() {
                this.inEditZone = {
                    id: null,
                    name: '',
                    k8s: '',
                    extensions: {
                        dashboard: '',
                        cluster: '',
                        level: 1
                    },
                    description: ''
                }
            },
            resetQueryForm() {
                this.currentEnv = '';
                this.currentValue = '';
                this.onSearch();
            }
        },

        data: function () {
            return {
                dialogVisible: false,
                currentField: 'id',
                currentValue: '',
                currentEnv: '',
                fieldList: [{
                    value: 'id',
                    label: 'ID'
                }, {
                    value: 'name',
                    label: 'zone'
                }],
                currentPage: 1,
                pageSize: 10,
                inEditZone: {
                    id: null,
                    name: '',
                    env: null,
                    k8s: '',
                    extensions: {
                        dashboard: '',
                        cluster: '',
                        level: 1,
                        rpc: ''
                    },
                    description: ''
                },
                rules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    env: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    k8s: [
                        {required: true, message: 'k8s', trigger: 'blur'}
                    ]
                }
            }
        },

        computed: {
            ...mapGetters({
                envs: 'getAllEnvs',
                zones: 'getAllZones',
                total: 'getTotalZones',
                isAdmin: 'getAdminResult'
            })
        },
        created() {
            this.$store.dispatch('fetchAllEnvs', {
                pageSize: 9999,
                page: 1,
            });
            this.$store.dispatch('fetchAllZones', {
                pageSize: this.pageSize,
                page: this.currentPage,
            });
        }

    }

</script>

<style>
    .query-form {
        margin-top: 20px;
        padding: 25px;
        background-color: #eef1f6;
    }

    .close-button {
        float: right;
    }

    .add_button {
        float: right;
    }
</style>




